.drawer-container {
  position: relative;
  height: 400px;
  border: 1px solid #ccc;
}
.drawer-container .main {
  position: relative;
  padding: 10px;
  box-sizing: border-box;
  height: 100%;
}
.rc-drawer-sidebar {
  padding: 16px;
  background-color: #e4f6fe;
}
.rc-drawer-left .rc-drawer-sidebar,
.rc-drawer-right .rc-drawer-sidebar {
  height: 100%;
}



